ry: #a8a8d4;\n\n\n$clr-alert: (\n  \"clr-alert-1\": #0a2e46cc,\n  \"clr-alert-2\": #0a610acc,\n  \"clr-alert-3\": #8e9627cc,\n  \"clr-alert-4\": #835417cc,\n  \"clr-alert-5\": #971010cc,\n);\n\n\n@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100..700;1,100..700&display=swap');\n\n@mixin flex-container($direction: row, $justify: center, $align: center) {\n  display: flex;\n  flex-direction: $direction;\n  justify-content: $justify;\n  align-items: $align;\n}\n\n@mixin box-shadow($color: #000000aa, $top: 0, $left: 0, $blur: 10px, $spread: 0px) {\n  box-shadow: $top $left $blur $spread $color;\n}\n\n@mixin button($width: 24px, $height: 24px, $image-size: 80%, $hover-bg: #00000033) {\n  @include flex-container;\n  width: $width;\n  height: $height;\n\n  border-radius: 8px;\n  background-color: transparent;\n  scale: 1;\n  transition: background-color 0.3s ease, scale 0.3s ease;\n  cursor: pointer;\n\n  img {\n    height: $image-size;\n  }\n\n  &:hover {\n    background-color: $hover-bg;\n  }\n\n  &:active {\n    scale: 0.9;\n  }\n}\n\n@mixin delete-button() {\n  :hover {\n    filter: invert(9%) sepia(94%) saturate(5163%) hue-rotate(2deg) brightness(92%) contrast(113%);\n  }\n}\n\n@mixin checkbox($size: 20px, $circle: true) {\n  width: $size;\n  height: $size;\n\n  @include box-shadow(#000000, 0px, 0px, 2px, 0.3px);\n  background-color: white;\n  appearance: none;\n  outline: none;\n  cursor: pointer;\n\n  &:checked {\n    appearance: auto;\n    accent-color: lighten($clr-primary, 20%);\n  }\n\n  @if $circle {\n    border-radius: 50%;\n\n    &:checked {\n      clip-path: circle(50% at 50% 50%);\n    }\n  }\n}\n\n@mixin new-button($hover-clr: #00000011) {\n  order: 1;\n\n  @include flex-container(row, start);\n  gap: 4px;\n  width: 100%;\n  padding: 6px 8px;\n  \n  border-radius: 8px;\n  opacity: 50%;\n\n  cursor: pointer;\n  user-select: none;\n  transition: background-color 0.2s ease;\n\n  img {\n    height: 20px;\n  }\n\n  p {\n    font-size: 0.9rem;\n    font-weight: bold;\n    padding-top: 2px;\n  }\n\n  &:hover {\n    background-color: $hover-clr;\n  }\n}\n\n* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\nimg {\n  -webkit-user-drag: none;\n  user-select: none;\n  -moz-user-select: none;\n  -webkit-user-select: none;\n  -ms-user-select: none;\n}\n\n.template {\n  display: none !important;\n}\n\nbody {\n  display: grid;\n  grid-template: 100px 1fr / 300px 1fr;\n\n  font-family: \"Josefin Sans\", sans-serif;\n  min-height: 100vh;\n\n  overflow: hidden;\n\n\n  ::-webkit-scrollbar {\n    width: 8px;\n  }\n\n  /* Track */\n  ::-webkit-scrollbar-track {\n    box-shadow: inset 0 0 5px #777;\n    border-radius: 10px;\n  }\n\n  /* Handle */\n  ::-webkit-scrollbar-thumb {\n    background: rgba($clr-primary, 0.6);\n    border-radius: 16px;\n  }\n}\n\n.header {\n  @include box-shadow(#00000077);\n  @include flex-container;\n  grid-column: span 2;\n\n  background-color: $clr-primary;\n  padding: 24px 36px;\n\n  position: relative;\n  z-index: 1;\n\n  user-select: none;\n\n  .sidebar-btn {\n    @include flex-container;\n    @include button(50px, 50px, 70%);\n    position: absolute;\n    left: 18px;\n  }\n\n  .logo {\n    @include flex-container;\n    gap: 12px;\n\n    &__icon {\n      height: 64px;\n    }\n\n    &__text {\n      color: white;\n      font-size: 2.1rem;\n      letter-spacing: 1px;\n    }\n  }\n}\n\n.sidebar {\n  @include box-shadow(#00000055);\n  @include flex-container(column, start, start);\n\n  background-color: $clr-secondary;\n  padding: 24px 36px;\n\n  user-select: none;\n\n  &--hidden {\n    display: none;\n  }\n\n  &--hidden + .main {\n    grid-column-start: 1;\n  }\n\n  &__section-header {\n    color: $clr-primary;\n\n    width: 100%;\n    margin-bottom: 8px;\n    border-bottom: 2px solid $clr-primary;\n  }\n\n  &__section {\n    @include flex-container(column, start, start);\n    gap: 4px;\n    padding-left: 8px;\n    margin-bottom: 32px;\n    width: 100%;\n\n\n    .filter {\n      @include flex-container(row, start);\n      gap: 8px;\n      flex: none;\n\n      height: 40px;\n      width: 100%;\n      padding: 6px 8px;\n      border-radius: 8px;\n\n      background-color: transparent;\n      transition: background-color 0.1s ease;\n      cursor: pointer;\n\n      &__icon {\n        height: 28px;\n      }\n\n      &__title {\n        font-size: 1.25rem;\n        padding-top: 4px;\n\n        transform: translateY(2px);\n      }\n\n      &--active {\n        background-color: #8686d8;\n        border-radius: 0px 8px 8px 0px;\n        border-left: $clr-primary 3px solid;\n      }\n\n      &:hover:not(&--active) {\n        background-color: darken($clr-secondary, 5%);\n      }\n    }\n\n\n    &--projects {\n      max-height: 180px;\n      overflow-x: hidden;\n      overflow-y: auto;\n\n      &__new-btn {\n        @include new-button(darken($clr-secondary, 5%));\n        height: 40px;\n        opacity: 60%;\n      }\n\n      .filter {\n        padding-right: 16px;\n\n        &__title {\n          max-width: 200px;\n          margin-right: auto;\n          white-space: nowrap;\n          overflow: hidden;\n          cursor: text;\n          \n          &:focus {\n            @include box-shadow(#00000077, 0, 0, 2px, 1px);\n            border: none;\n            outline: none;\n          }\n        }\n\n        &__btn {\n          @include button(28px, 28px, 70%, #00000022);\n          display: none;\n          flex: none;\n          opacity: 90%;\n\n          &--delete {\n            @include delete-button();\n          }\n        }\n\n        &:hover .filter__btn {\n          @include flex-container;\n        }\n      }\n    }\n  }\n}\n\n.main {\n  display: grid;\n  grid-template: max-content 1fr / 1fr 300px;\n\n  padding: 36px;\n  grid-column-end: -1;\n  background-color: #fefefe;\n\n  &__header {\n    margin-bottom: 18px;\n    max-width: 800px;\n    overflow: hidden;\n    white-space: nowrap;\n\n    color: $clr-primary;\n    font-size: 2.2rem;\n  }\n\n  .completed-wrapper {\n    justify-self: right;\n    align-self: start;\n\n    @include flex-container;\n    gap: 8px;\n  }\n\n  .completed-switch-label {\n    color: #555;\n  }\n\n  .completed-switch {\n    $height: 24px;\n    $width: 40px;\n\n    position: relative;\n\n    height: $height;\n    width: $width;\n\n    @include box-shadow(#00000055, 0, 0, 2px, 0);\n    border-radius: 32px;\n    overflow: hidden;\n\n    &__checkbox {\n      display: none;\n\n      &:checked + .completed-switch__slider {\n        background-color: rgba($clr-primary, 0.8);\n      }\n      \n      &:checked + .completed-switch__slider:before {\n        transform: translateX(calc($width - $height));\n      }\n    }\n\n    &__slider {\n      position: absolute;\n      top: 0;\n      left: 0;\n      bottom: 0;\n      right: 0;\n      \n      background-color: #aaa;\n      transition: 0.3s;\n      cursor: pointer;\n\n      &::before {\n        content: \"\";\n        position: absolute;\n        height: calc($height - 8px);\n        width: calc($height - 8px);\n        left: 4px;\n        bottom: 4px;\n\n        border-radius: 50%;\n        background-color: white;\n        transition: .4s;\n      }\n    }\n  }\n\n  .task-list {\n    grid-column: 1 / -1;\n\n    @include flex-container(column, start, center);\n    gap: 12px;\n    height: 380px;\n    width: 100%;\n    overflow-x: hidden;\n    overflow-y: auto;\n\n    &__new-btn {\n      @include new-button();\n    }\n\n    .task {\n      @include flex-container(row, start);\n      flex: none;\n      gap: 12px;\n      width: 100%;\n      height: 40px;\n      padding: 8px 12px;\n\n      @include box-shadow(#00000033, 1px, 1px, 5px);\n      background-color: lighten($clr-secondary, 24%);\n      border-radius: 2px 8px 8px 2px;\n      // border-left: 4px solid map-get($map: $clr-alert, $key: \"clr-alert-1\");\n\n      &__is-done {\n        @include checkbox();\n      }\n\n      &__is-done:checked ~ .task__title,\n      &__is-